// 首页推荐底部！！
<template>
  <div class="Game_scroll">
    <div :class="{'hovers':itemNo<=0}">
      <img src="../../../static/images/category/111.png" alt=""  class="img1">
    </div>
    <div class="game_topbox">
      <div id="game_box">
        <div
          v-for="(item,index) in top"
          :key="item.elementId"
          :id="('Game'+index)"
          class="Game_small"
            >
            <img
            :src='top[index].elementImg'
          />
          <div :class="{'hover':itemNo==index&&isActive}"></div>
        </div>
      </div>
    </div>
    <div :class="{'hovers':itemNo==(list-1)||list<=6}">
      <img  src="../../../static/images/category/222.png" alt=""  class="img2">
    </div>

    <keyDo
      v-on:listenKeyCode="keyCode"
      v-if="isShow"
    ></keyDo>
  </div>
</template>
<style scoped="bottom">
.hovers{
  display: none;
}
  .Game_scroll{
    width: 1280px;
    height: 100px;
    position: relative;
    margin-left: 18px;

  }
  .game_topbox{
    width: 1160px;
    height: 100px;
    overflow: hidden;
  }
  #game_box{
    width: 2000px;
    height: 100px;
  }

  .img1{
    width: 80px;
    height: 80px;
    position: absolute;
    left: -70px;
    top: 10px;
  }
  .img2{
     width: 80px;
    height: 80px;
    position: absolute;
    right: 50px;
    top: 10px;
  }
  .Game_small{
    width: 178px;
    height: 96px;
    background: url(../../../static/images/category/649.png) no-repeat 100% 100%;
    float: left;
    margin-right: 18px;
    font-size: 20px;
    text-align: center;
    line-height: 89px;
    position: relative;
  }

  .hover{
    width: 178px;
    height: 96px;
    background: url(../../../static/images/category/1892.png) no-repeat;
    background-size: 100% 100%;
    z-index: 99;
    position: absolute;
    left: 0px;
    top: 0px;
  }
</style>

<script>
import { mapGetters } from 'vuex';
import c from '../../js/common.js';
import {yst} from '../../js/yst.js';
import { client as yh} from '../../js/client.js'; // 播放器
import {bi} from '../../js/logApi.js'
export default {
  props: ['initData', 'itemSort', 'catId'],
  name: 'games_scroll',
  data: function () {
    return {
      name: 'games_scroll',
      placeholder: require('../../assets/img_loading_352x190.png'),
      initScore: require('../../assets/corner_mark.png'),
      itemNo: 0,
      pos: '',
      eleId: '',
      pageId: '',
      list: 0,
      top: [
        {
          elementId: '',
          elementImg: require('../../assets/img_loading_352x190.png'),
          elementName: '',
          elementType: '',
          jsonUrl: '',
          layout: '',
          score: ''
        }
      ],
      isShow: false, // 是否注册键值
      isActive: false, // 是否被激活,
      load_start: 0,
      load_time: 0,
      startTime: 0,
      posId: '',
      click_type: ''
    };
  },
  computed: {
    ...mapGetters(['behindParams', 'navpos', 'categorys', 'isBack', 'shouy'])
  },
  created () {
    this.load_start = new Date() - 0;
  },

  mounted () {
    this.load_time = new Date() - this.load_start;
    this.top = this.initData.elementList;
    this.list = this.top.length;
    this.eleId = this.initData.specialId;

  },

  methods: {
    keyCode (kc) {
      if (kc == 'KeyBack') {
        this.back();
      } else if (kc == 'right') {
        this.right();
      } else if (kc == 'left') {
        this.left();
      } else if (kc == 'down') {
        this.down();
      } else if (kc == 'up') {
        this.up();
      } else if (kc == 'KeyEnter') {
        this.enter();
      }
    },
    enter (){
      if (this.navpos == 0){
        if (!this.isBack) return;
      }
      this.posId = this.itemSort + '0' + (this.itemNo + 1);
      let columnId = '0' + this.navpos;
      if ( this.categorys[this.navpos].catId == 204121 ) {
         c.setPosition(columnId, this.posId); // 保存推荐位置
       localStorage.setItem('BI_recmd_id', '');
      } else {
         c.setPosition(columnId, ''); 
         localStorage.setItem('BI_recmd_id', '');
      }
      c.setParentPageType('0101');
      c.setParentPageId(this.categorys[this.navpos].catId);
      c.setJump();
      this.$store.commit('GET_ISSHOWPLAY', false);
      this.$store.commit('GET_SHOUY', false);
      // 关闭小窗口播放
      let recmd_id = '';
      let click_id = '';
      // if (this.initData.elementList[this.itemNo].layout === undefined){
      //   this.click_type = 102;
      //   click_id = '102-1'
      // } else if (this.initData.elementList[this.itemNo].layout === 'Subject_Detail_TP1'){
      //   this.click_type = 2;
      //   click_id = this.initData.elementList[this.itemNo].elementId
      // } else {
      //   this.click_type = 1;
      //   click_id = this.initData.elementList[this.itemNo].elementId
      // }
      let t = this.initData.elementList[this.itemNo].elementType;
      if (t === undefined){
        this.click_type = 102;
        click_id = '102-1'
      } else if (t == 4 || t == 5 ){
        this.click_type = 2;
        click_id = this.initData.elementList[this.itemNo].elementId
      } else if ( t == 2 ){
        this.click_type = 3;
        click_id = this.initData.elementList[this.itemNo].elementId
      } 
      else {
        this.click_type = 1;
        click_id = this.initData.elementList[this.itemNo].elementId
      }
      let biObj = {
        stay_time: new Date() - this.startTime,
        recmd_id: 0,
        pos_id: this.itemSort + '0' + (this.itemNo + 1),
        page_type: '0101',
        page_id: this.catId,
        click_type: this.click_type,
        click_id: click_id
      }
      // bi.jump(biObj);
      if ( this.categorys[this.navpos].catId == 204121 ) {
        this.jumpRec();
      }
      if (this.top[this.itemNo].jsonUrl) {
        c.routerSkip(this.top[this.itemNo].jsonUrl, this.top[this.itemNo].elementType, this.top[this.itemNo].layout, {}, this.$router);
      } else {
        c.routerSkip({elementCode: this.top[this.itemNo].elementCode, elementId: this.top[this.itemNo].elementId}, this.top[this.itemNo].elementType, '', {}, this.$router);
      }
    },
    jumpRec(){
          let recommendBiObj = {
            pos_id: this.posId,
            recmd_id: '',
            page_type: '0101',
            page_id: this.catId,
            click_type: this.click_type,
            cid: this.top[this.itemNo].elementId
          }
          bi.jumpRecommend( recommendBiObj );
    },
    back () {
      this.isActive = false;
      this.isShow = false;
      this.$emit('pressDown', this.isShow, this.eleId, 'back');
    },
    right (){
      if (this.itemNo >= this.top.length - 1) {
        return;
      } else {
        this.itemNo++;
      }
      this.addscroll();
    },
    left (){
      if (this.itemNo <= 0) return;
      this.itemNo--;
      this.addscroll();
    },
    up (){
      this.isActive = false;
      this.isShow = false;
      this.$emit('pressDown', this.isShow, this.eleId, 'up');
    },
    down (){
      this.isActive = false;
      this.isShow = false;
      this.$emit('pressDown', this.isShow, this.eleId, 'down');
    },
    updateStast (bl, pos, isEnd) {
      console.log('游戏底部部组件接受键值' + bl + pos + isEnd);
      if (bl) {
        this.isShow = bl;
        this.isActive = true;
        this.pos = pos;
      }
    },
    clickPage () {
      var ystObj = {};
      ystObj.time = new Date().format('yyyy-MM-dd hh:mm:ss');
      ystObj.page_name = 'pageIndex_0' + (this.navpos + 1);
      ystObj.page_level = '1';
      ystObj.Panel_id = '0' + (this.navpos + 1);
      ystObj.Recommend_id = ystObj.Panel_id + this.itemSort + '0' + (this.itemNo + 1);
      ystObj.Page_url = window.location.href;
      ystObj.Load_time = this.load_time;
      ystObj.Load_status = 'success';
      yst.panelClick(ystObj);
    },
    addscroll (){
      let top = this.cEleOffsetTop('Game' + this.itemNo);
      var game = document.getElementById('game_box');
      if (top > 1100) {
        game.style.marginLeft = -(this.itemNo - 5) * 196 + 'px'
        // game.style.setProperty({'transform': 'translateX(-' + 164 * (this.itemNo-5) + 'px)','transition': 'all 0.3s'});
      }
      if (top < 173.9921875) {
        game.style.marginLeft = -(this.itemNo) * 196 + 'px'
      }
    },
    cEleOffsetTop (ele) {
      try {
        var elsss = document.getElementById(ele);
        let fEleDistance = elsss.offsetLeft;
        let realHeight = fEleDistance;
        return realHeight;
      } catch (error) {
        console.log('获取高度异常')
      }
    }
  },
  watch: {
    initData () {
      this.top = this.initData.elementList;
      this.eleId = this.initData.specialId;
      this.itemNo = 0;
      this.list = this.top.length;
      this.addscroll();
      this.startTime = new Date() - 0;
    }
  }
};
</script>



